@import "../../assets/style/app";

.app {
  background: #405F96;
  position: relative;
  padding-bottom: 30px;

  @include desktop {
    padding-bottom: 36px;
  }

  .cardWrapper {
    padding-top: 80px;

    @include desktop {
      padding-top: 130px;
    }

    .card {
      overflow: hidden;

      @include desktop {
        width: 1270px;
        margin-left: auto;
        margin-right: auto;
        border: 7px solid #8491AF;
        border-radius: 28px;
      }

      .bottom {
        position: relative;
        background-image: url(../../assets/png/card/back.png);
        background-size: cover;
        background-position: center;
        padding-bottom: 44px;

        @include desktop {
          padding-bottom: 100px;
        }

        .bottomInner {
          width: 100%;
          height: 100%;
          position: relative;
          z-index: 10;
        }
      }

    }
  }

  .right {
    margin-top: 30px;
    @include RetroLandMayhem;
    font-size: 13px;
    line-height: 150%;
    text-transform: capitalize;
    color: #BDBDBD;
    text-align: center;

    @include desktop {
      margin-top: 35px;
      font-size: 16px;
    }
  }
}
