@import "../../assets/style/app";

.timer {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  background-image: url(../../assets/png/B2_Timer/back.png);
  background-size: cover;
  background-position: center;
  height: 643px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 26px;

  @include desktop {
    height: 743px;
    padding-bottom: 53px;
  }

  .title {
    position: relative;
    z-index: 10;
    @include H2;
  }

  .time {
    position: relative;
    z-index: 10;
    @include Mayak;
    font-size: 31px;
    line-height: 150%;
    color: #FCC23B;

    @include desktop {
      font-size: 64px;
    }
  }

}
