@import "../../assets/style/app";

.video {
  padding-top: 80px;
  padding-left: 24px;
  padding-right: 24px;

  @include desktop {
    padding-top: 120px;
    padding-left: 24px;
    padding-right: 24px;
  }

  .inner {
    display: flex;
    justify-content: center;

    box-sizing: border-box;
    border-top: 2px solid #182238;
    border-bottom: 2px solid #182238;
    height: 190px;

    @include desktop {
      border-width: 8px;
      width: 1270px;
      height: 772px;
      margin-left: auto;
      margin-right: auto;
    }

    img {
      display: block;
      width: 310px;
      height: auto;

      @include desktop {
        width: 1264px;
      }
    }

  }
}
